.navigation {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 1rem 1rem;
    background-color: rgb(10,26,92);
    color: white;
    box-shadow: 0 2px 2px 2px white;
  }
  
  .brand-name {
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
    margin-left: 1rem;
  }
  .navigation-menu {
    margin-left: auto;
  }

  .navigation-menu ul {
    display: flex;
    padding: 0;
  }
  .navigation-menu li {
   
    margin: 0 1rem;

  }
  .navigation-menu li a {
  
    text-decoration: none;
    display: block;
    width: 100%;
    color: aliceblue;
  }

  .navigation-menu li :hover {

    color: #2642af;;
  }
  .hamburger {

    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: #070a18;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;

    position: absolute;
    top: 50%;
    right: 25px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: none;
  }
  .hamburger:hover {
    background-color: #2642af;
  }
body {
    margin: 0;
    padding: 0;
  }
  
  .Login {
    height: 500px;
    min-height: 100vh;
    background-image: url(/static/media/Background.ef0c9670.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: sans-serif;
  }
  
  .form {
    padding: 40px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
  }
  
  .input-container {
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 14px;
  }
  
  .label {
    margin-bottom: 5px;
  }
  
  .input {
    padding: 5px 15px 5px 5px;
  }
  
  .forgotten-password {
    align-self: flex-end;
    margin-top: 7px;
  }
  
  #login-btn {
    align-self: center;
    padding: 7px 30px;
    margin-top: 30px;
    border: none;
    color: #fff;
    background-color:  rgb(10,26,92);;
    box-shadow: 2px 2px 2px rgb(10,26,92);;
    cursor: pointer;
    text-transform: uppercase;
    transition: all 0.2s ease-in;
  }
  a:link {
    color: #fff;
   }
  #login-btn  .link{
      color: #fff;
  }
  #login-btn:hover {
    background-color: #2642af;
    box-shadow: none;
    -webkit-transform: translateX(1px) translateY(1px);
            transform: translateX(1px) translateY(1px);
  }
  
  .signup-label {
    position: relative;
    bottom: -10px;
    color: #fff;
    font-size: 14px;
  }
  
  .link {
    text-decoration: none;
    color: rgb(204, 4, 121);
  }
  
  .link:hover {
    text-decoration: underline;
  }
  
